@import "src/colors";
@import "src/grid";

$btn-round-size: 40px;

body {
  background: url("/images/wave.svg") no-repeat;
  background-size: cover;
}

.icon-list {
  .item {
    display: flex;
    align-items: center;
    padding: 4px;

    i {
      margin-right: 8px;
    }

    p {
      margin-bottom: 0;
    }
  }
}

.btn-round-row {
  display: flex;
  flex-wrap: wrap;
  width: 70%;

  margin: 0 -6px;

  a {

    margin: 6px 6px;
  }
}

.btn-round {
  border-radius: 50%;
  width: $btn-round-size;
  height: $btn-round-size;

  display: flex;
  justify-content: center;
  align-items: center;
}

.skills {
  display: flex;
  flex-wrap: wrap;
  width: 70%;

  margin: 0 -6px;

  .item {
    border-radius: .25rem;
    padding: 2px 8px;
    color: $custom-white;
    background-color: $custom-gray;

    margin: 6px 6px;
  }
}
